export const BitcoinService = {
    getRate,
    getMarketPrice,
    getTradesVolumes
}

async function getRate(coins) {
    var rate = utilService.loadFromStorage('bitcoinRate')
    if (rate) {
        return rate
    } else {
        rate = await axios.get('https://blockchain.info/tobtc?currency=USD&value=1')
        utilService.storeToStorage('bitcoinRate', rate.data)
        return rate.data
    }
}

async function getMarketPrice() {
    var marketPrices = utilService.loadFromStorage('marketPrices')
    if (marketPrices) {
        return marketPrices
    } else {
       marketPrices = await axios.get('https://api.blockchain.info/charts/market-price?timespan=5months&format=json&cors=true')
        utilService.storeToStorage('marketPrices', marketPrices.data.values)
        return marketPrices.data.values
    }
}

async function getTradesVolumes() {
    var tradesVolumes = utilService.loadFromStorage('tradesVolumes')
    if (tradesVolumes) {
        return tradesVolumes
    } else {
        tradesVolumes = await axios.get('https://api.blockchain.info/charts/trade-volume?timespan=5months&format=json&cors=true')
        utilService.storeToStorage('tradesVolumes', tradesVolumes.data.values)
        return tradesVolumes.data.values
    }
}